import {Component, Input, OnInit} from '@angular/core';
import {Playlist, PlaylistService} from '../../../services/api/playlist.service';
import {AlertController, ModalController, PopoverController} from '@ionic/angular';
import {FileService, File} from '../../../services/api/file.service';

@Component({
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    @Input() public file: File;
    public playlists: Playlist[];
    public canImport: boolean;

    public selected: boolean[] = [];
    public allSelected = false;


    constructor(private fileService: FileService,
                private popoverController: PopoverController,
                private playlistService: PlaylistService,
                private alertController: AlertController,
                private modalController: ModalController
    ) {
    }

    ngOnInit() {
        // Load Playlists
        // const fileId = this.file.id;
        this.playlistService.with(this.fileService).subscribe(playlists => {
            this.playlists = playlists.map(playlist => {
                playlist.disabled = playlist.files.find(f => f.id == this.file.id) !== undefined;
                return playlist;
            });
        });
    }

    async close() {
        this.modalController.dismiss();
    }

    async add() {
        this.playlistService.create().then(async playlist => {
            this.update(playlist).then(response => {
                this.close();
            });
        });
    }

    async onchange() {
        this.canImport = this.selected.filter(a => !!a).length > 0;
        // this.allSelected = list.length === list.filter(v => v).length;

    }

    select() {
        let playlists = Object.keys(this.selected);
        playlists.filter(id => !!this.selected[id]).map((id: any) => {
            return this.playlists.find((playlist) => {
                return playlist.id == id;
            })
        }).forEach((playlist: Playlist) => {
            this.update(playlist);
        });

        this.close();
    }

    update(playlist: Playlist) {
        return playlist.attach(this.file);
    }
}
