import {Component, OnInit} from '@angular/core';
import {ActionsComponent} from './actions/actions.component';
import {ModalController, PopoverController} from '@ionic/angular';
import {File, FileService} from '../../services/api/file.service';
import {AddComponent} from './add/add.component';
import {Order, Resource} from '../../providers/api/resource';
import {PreviewComponent} from "../../components/preview/preview.component";
import {ImportComponent} from "../../components/import/import.component";
import {UploadComponent} from "../../components/upload/upload.component";
import {PlayerService} from "../../services/player.service";

@Component({
    selector: 'app-files',
    templateUrl: './files.page.html',
    styleUrls: ['./files.page.scss'],
})
export class FilesPage implements OnInit {
    public searchTerm: string;
    public files: File[] = [];

    private searcher: Resource<File>;

    constructor(private fileService: FileService,
                private popoverController: PopoverController,
                private modalController: ModalController,
                public playerService: PlayerService) {
    }

    ngOnInit() {
    }

    ionViewWillEnter() {
        this.searcher = this.fileService;
        if (this.searchTerm) {
            this.searcher.where({
                filename: {
                    operator: 'like',
                    value: '%' + this.searchTerm + '%'
                }
            });
        }
        this.searcher.orderBy('id', Order.DESC)
            .limit(100)
            .page(1)
            .subscribe(result => {
                this.files = result;
            });
    }

    ionViewWillLeave() {
        this.searcher.unsubscribe();
    }


    search() {
        this.searcher.search([
            {
                filename: {
                    operator: 'like',
                    value: '%' + this.searchTerm + '%'
                }
            }
        ]);
    }

    async add(ev: any, file?) {
        return (await this.modalController.create({
            component: AddComponent,
            cssClass: 'add-modal',
            componentProps: {
                file: file
            }
        })).present();
    }

    async actions(ev: any, file?) {
        return (await this.popoverController.create({
            component: ActionsComponent,
            componentProps: {
                file: file
            },
            event: ev,
            translucent: false,
            mode: 'ios',
        })).present();
    }

    async preview(file) {
        return (await this.modalController.create({
            component: PreviewComponent,
            cssClass: 'preview-modal',
            componentProps: {
                'fileId': file.id
            }
        })).present();
    }

    async importDevice() {
        const modal = await this.modalController.create({
            component: ImportComponent,
            animated: true,
            backdropDismiss: false,
            keyboardClose: false
        });
        return await modal.present();
    }

    async importUpload() {
        const modal = await this.modalController.create({
            component: UploadComponent,
            animated: true,
            backdropDismiss: false,
            keyboardClose: false
        });
        return await modal.present();
    }
}
