import {Component, OnInit, ViewChild} from '@angular/core';
import {IonContent, ModalController} from '@ionic/angular';
import {FileUploader, FileLikeObject} from 'ng2-file-upload';
import {HttpClient} from "@angular/common/http";
import {APP_CONFIG} from "../../app-routing.module";
import {File, FileService} from "../../services/api/file.service";


@Component({
    selector: 'app-upload',
    templateUrl: './upload.component.html',
    styleUrls: ['./upload.component.scss'],
})
export class UploadComponent implements OnInit {
    @ViewChild('content', { static: true }) content: IonContent;

    public uploader: FileUploader = new FileUploader({});

    public importItems: File[];
    public processed: boolean[] = [];
    public importCompleted = false;

    constructor(
        private modalController: ModalController,
        public httpClient: HttpClient,
        private fileService: FileService
    ) {
    }

    ngOnInit() {

    }

    getFiles(): FileLikeObject[] {
        return this.uploader.queue.map((fileItem) => {
            return fileItem.file;
        });
    }

    async upload() {
        const formData = new FormData();
        let files = this.getFiles();
        files.forEach((file) => {
            formData.append('files[]', file.rawFile, file.name);
        });

        await this.httpClient.post(APP_CONFIG.host + '/api/v1/upload', formData).toPromise().then(async (files) => {

            this.importItems = <File[]>files;
            let i = 0;
            for (const item of this.importItems) {
                await this.fileService.create({filepath: item.filepath, filename: item.filename})
                    .then(() => this.processed[item.integrity_hash] = true)
                    .catch(() => this.processed[item.integrity_hash] = false);
                this.content.scrollToPoint(null, i * 50, 300);
                i++;
            }

            const importer = this;
            setTimeout(function () {
                importer.importCompleted = true;
                setTimeout(() => {
                    importer.modalController.dismiss();
                }, 1500);
            }, 1500);
        });
    }


    async close() {
        this.modalController.dismiss();
    }

}
