import {Injectable} from '@angular/core';
import {Resource} from '../../providers/api/resource';
import {File} from './file.service';
import {Entity} from '../../providers/api/entity';

export interface Device extends Entity<Device> {
    id: string;
    label?: string;
    size: number;
    location: string;
    files: File[];
}

@Injectable({
    providedIn: 'root'
})
export class DeviceService extends Resource<Device> {
    public name = 'devices';
    public location = '/api/v1/devices';
}
