import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {AlertController, IonContent, LoadingController, ModalController} from '@ionic/angular';
import {Device, DeviceService} from '../../services/api/device.service';
import {File, FileService} from '../../services/api/file.service';

@Component({
    selector: 'app-import',
    templateUrl: './import.component.html',
    styleUrls: ['./import.component.scss'],
})
export class ImportComponent implements OnInit {
    @Input('deviceId') deviceId: string;
    @ViewChild('content', { static: true }) content: IonContent;
    public searchTerm: string;
    public devices: Device[];
    public files: File[];
    public result: File[];
    public canImport: boolean;

    public selected: boolean[] = [];
    public allSelected = false;

    public importItems: File[];
    public processed: boolean[] = [];
    public importCompleted = false;

    constructor(
        private alertController: AlertController,
        private modalController: ModalController,
        private deviceService: DeviceService,
        private fileService: FileService,
        private loadingCtrl: LoadingController
    ) {
    }

    ngOnInit() {
        this.deviceService.subscribe(devices => {
            this.devices = devices;
            if (devices.length === 1) {
                // this.openDevice(devices[0].id);
            }

        });
    }

    async close() {
        if (this.importItems && !this.importCompleted) {
            const alert = await this.alertController.create({
                header: 'Are you sure?',
                message: 'Are you sure you want to stop importing the files?',
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Stop Import',
                        handler: () => {
                            this.modalController.dismiss();
                        }
                    }
                ]
            });
            await alert.present();
        } else {
            this.modalController.dismiss();
        }
    }

    reset() {
        this.deviceId = null;
        this.files = null;
        this.result = null;
        this.selected = [];
    }

    async openDevice(id: string) {
        this.selected = [];
        const loader = await this.loadingCtrl.create();
        await loader.present();

        this.deviceService.with(this.fileService).find(id).then(async (device) => {
            this.files = device.files;
            this.deviceId = id;
            this.selected = [];
            this.search();

            await loader.dismiss();
        });


    }

    search() {
        this.result = this.files.filter(file => {
            if (!this.searchTerm) {
                return true;
            }
            return file.filename.toLowerCase().indexOf(this.searchTerm.toLowerCase()) > -1;
        });
    }

    async onchange(all) {
        if (all) {
            const list = this.result.map(item => (this.selected[item.integrity_hash] ? this.selected[item.integrity_hash] : false));
            const isAll = list.length === list.filter(v => v).length;
            this.files.forEach(item => {
                this.selected[item.integrity_hash] = !isAll;
            });
        }

        const list = this.result.map(item => (this.selected[item.integrity_hash] ? this.selected[item.integrity_hash] : false));
        this.allSelected = list.length === list.filter(v => v).length;
        this.canImport = list.filter(v => v).length > 0;
    }

    select(all = false) {
        if (all) {
            const list = this.result.map(item => (this.selected[item.integrity_hash] ? this.selected[item.integrity_hash] : false));
            const isAll = list.length === list.filter(v => v).length;
            this.files.forEach(item => {
                this.selected[item.integrity_hash] = !isAll;
            });
        } else {
            const list = this.result.map(item => (this.selected[item.integrity_hash] ? this.selected[item.integrity_hash] : false));
            this.allSelected = list.length === list.filter(v => v).length;
            this.canImport = list.filter(v => v).length > 0;
        }
        // setTimeout(() => {

        // }, 100);
    }

    async startImport() {
        this.importItems = <File[]>this.result.map(item => this.selected[item.integrity_hash] ? item : false).filter(v => v);
        let i = 0;
        for (const item of this.importItems) {
            await this.fileService.create({filepath: item.filepath, filename: item.filename})
                .then(() => this.processed[item.integrity_hash] = true)
                .catch(() => this.processed[item.integrity_hash] = false);
            this.content.scrollToPoint(null, i * 50, 300);
            i++;
        }

        const importer = this;
        setTimeout(function () {
            importer.importCompleted = true;
            setTimeout(() => {
                importer.modalController.dismiss();
            }, 1500);
        }, 1500);
    }
}
