import {Component, Input, OnInit} from '@angular/core';
import {AlertController, PopoverController} from '@ionic/angular';
import {FileService, File} from '../../../services/api/file.service';
import {Entity} from '../../../providers/api/entity';

@Component({
    templateUrl: './actions.component.html',
    styleUrls: ['./actions.component.scss'],
})
export class ActionsComponent implements OnInit {
    @Input() file: File & Entity<File>;

    constructor(private fileService: FileService,
                private popoverController: PopoverController,
                private alertController: AlertController) {
    }

    ngOnInit() {
    }


    async modify() {
        this.popoverController.dismiss().then(async () => {
            const alert = await this.alertController.create({
                header: 'Modify File',
                inputs: [
                    {
                        name: 'title',
                        type: 'text',
                        placeholder: 'Enter Title...',
                        value: this.file.title
                    },
                    {
                        name: 'artist',
                        type: 'text',
                        placeholder: 'Enter Artist...',
                        value: this.file.artist
                    }
                ],
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Modify',
                        handler: (data) => {
                            this.file.update({
                                title: data.title,
                                artist: data.artist
                            });
                        }
                    }
                ],
                cssClass: 'modal'
            });

            return await alert.present();
        });
    }

    async remove() {
        this.popoverController.dismiss().then(async () => {
            const alert = await this.alertController.create({
                header: 'Warning',
                message: 'Do you really want to delete this file?',
                buttons: [
                    {
                        text: 'Cancel',
                        role: 'cancel'
                    }, {
                        text: 'Delete',
                        handler: () => {
                            this.file.delete();
                        }
                    }
                ]
            });
            await alert.present();
        });

    }
}
